.secondSection {
  background: #f6f6f6;
  padding: 8rem 12rem;
}

.topSection {
  width: 100%;
  margin: auto;
}
.topSectionH5 {
  text-transform: uppercase;
  color: #16344e;
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 1rem;
}
.topSectionH1 {
  font-size: 3.5rem;
  text-transform: uppercase;
  width: 75%;
}
.searchBox {
  width: 86%;
  height: 45px;
  margin: 2rem 0;
}

.programsCount {
  color: #b08f29;
  font-size: 110px;
  font-family: "Oswald", sans-serif;
}

.programsCountWrapper {
  position: relative;
  display: inline-block;
}

.programsCountPlus {
  color: #ac9031;
  font-size: 3rem;
  font-weight: 400;
  position: absolute;
  top: -10px;
  right: -24px;
}
.cardOverlay {
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 50%,
    rgba(0, 0, 0, 0.4) 70%,
    rgba(0, 0, 0, 0.8) 100%
  );
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  border-radius: 10px;
}
.programsText {
  font-family: "Geist", sans-serif;
  font-size: 24px;
}
.admissionYearText {
  color: #018ce8;
}
.admissionSection {
  padding-left: 3rem;
}
.admissionSection h4 {
  font-size: 26px;
}
.admissionSection p {
  font-size: 18px;
  font-weight: 300;
}
.admissionSection a {
  padding: 8px 30px;
  font-size: 16px;
  font-family: "Noto Sans", sans-serif;
}
.bottomSectionH6 {
  background: #f6f6f6;
  width: fit-content;
  padding-right: 3rem;
  position: relative;
  bottom: 20px;
  font-size: 20px;
  right: 10px;
  margin: 0 !important;
  text-transform: uppercase;
}

.bannerLabel {
  color: #fff;
  border-left: 4px solid #ffc100;
  border-radius: 2px;
  width: 100%;
  font-family: Geist, sans-serif;
  font-size: 28px;
  font-weight: 700;
  position: absolute;
  bottom: 14px;
  padding-left: 10px;
  gap: 7px;
  left: 0;
}
.searchBox input::placeholder {
  font-size: 14px;
  opacity: 0.6;
  font-style: italic;
}

.cardImage {
  width: 100%;
  height: 100%;
}
.programsCountSection {
  margin: 3rem 0;
}
.programsCardsSection {
  justify-content: end;
}
.rightDesktopArrow {
  display: block;
}
.rightMobileArrow {
  display: none;
}
.showOnlyMobileCard {
  display: none;
}
.showOnlyMobileSubHeading {
  display: none;
}
@media (min-width: 1381px) and (max-width: 1919px) {
  .secondSection {
    padding: 8rem 11rem;
  }
  .topSectionH1 {
    font-size: 2.5rem;
  }
  .topSectionH5 {
    font-size: 16px;
  }
  .programsCount {
    font-size: 90px;
  }
  .programsText {
    font-size: 18px;
  }
  .bannerLabel {
    font-size: 20px;
  }
  .admissionSection h4 {
    font-size: 22px;
  }
  .schoolsList a {
    font-size: 16px;
  }
  .admissionSection p {
    font-size: 15px;
  }
  .admissionSection a {
    font-size: 15px;
  }
}
@media (min-width: 1040px) and (max-width: 1380px) {
  .secondSection {
    padding: 8rem 8rem;
  }
  .topSectionH1 {
    font-size: 2rem;
  }
  .topSectionH5 {
    font-size: 14px;
  }
  .programsCount {
    font-size: 80px;
  }
  .programsText {
    font-size: 16px;
  }
  .bannerLabel {
    font-size: 20px;
  }
  .programsCountPlus {
    font-size: 2.7rem;
    top: -20px;
    right: -20px;
  }
  .bottomSectionH6 {
    font-size: 18px;
  }
  .schoolsList a {
    font-size: 15px;
  }
  .admissionSection h4 {
    font-size: 20px;
  }
  .admissionSection p {
    font-size: 12px;
  }
  .admissionSection a {
    font-size: 12px;
  }
}
@media (min-width: 768px) and (max-width: 1040px) {
  .explore-program-section a {
    font-size: 14px;
  }
  .programsCardsSection {
    justify-content: space-between;
  }
  .programsCountSection {
    margin: 0;
  }
  .bottomSectionH6 {
    font-size: 14px;
  }
  .explore-program-section a {
    font-size: 14px;
  }
  .admissionSection h4 {
    font-size: 20px;
  }
  .admissionSection {
    padding-bottom: 16px;
    padding-left: 1rem;
    padding-right: 4rem;
    border-right: 1px solid #e9e4e4;
  }
  .admissionSection a {
    font-size: 14px;
  }
  .explore-program-section a {
    font-size: 14px;
  }
  .admissionSection p {
    font-size: 14px;
    font-weight: 300;
  }
  .searchBox {
    width: fit-content;
    height: 35px;
    margin: 1rem 0;
  }
  .courses-offered-module__x5Lqha__programsCountPlus {
    font-size: 1.5rem;
    top: -5px;
    right: -15px;
  }
  .secondSection {
    padding: 4rem 4rem;
  }
  .topSectionH5 {
    font-size: 12px;
    margin-bottom: 0.6rem;
  }
  .topSectionH1 {
    font-size: 25px;
  }
  .programsCount {
    font-size: 50px;
  }
  .programsText {
    font-size: 14px;
  }
  .bannerLabel {
    font-size: 15px;
  }
}
@media (max-width: 768px) {
  .showOnlyMobileSubHeading {
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 14px;
    padding: 0 1rem;
    margin: 0;
  }
  .topSectionH5 {
    display: none;
  }
  .secondSection {
    padding: 0;
    background: #16344e;
    padding-bottom: 5rem;
  }
  .topSection {
    background: #f6f6f6;
    padding: 2.5rem 1rem;
    margin: 0 !important;
    padding-bottom: 1rem;
  }
  .topSectionH1 {
    width: 100%;
    font-size: 34px;
    text-align: center;
  }
  .searchBox {
    display: none;
  }
  .programsCountSection {
    display: none !important;
  }
  .exploreProgramSectionWrapper {
    display: none;
  }
  .programsCardsSection {
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 12px !important;
    padding: 0;
  }
  .second-section-cards-image {
    width: 45%;
  }
  .programsCardsSection a {
    width: 47%;
  }
  .bannerLabel {
    font-size: 20px;
    justify-content: space-between;
    padding-right: 10px;
    border: unset;
  }
  .rightDesktopArrow {
    display: none;
  }
  .rightMobileArrow {
    display: block;
  }
  .showOnlyMobileCard {
    background: #fff;
    border-radius: 30px 4px 10px 10px;
    width: 45%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    flex-direction: column;
    position: relative;
  }
  .lastCardArrow {
    position: absolute;
    bottom: 0;
  }
  .cardImage {
    width: 100%;
    height: 100%;
    border-top-left-radius: 30px;
  }
  .cardOverlay {
    background: linear-gradient(
      rgba(0, 0, 0, 0) 50%,
      rgba(0, 0, 0, 0.4) 70%,
      rgba(0, 0, 0, 0.8) 100%
    );
    border-radius: 4px;
  }
  .showOnlyMobileCard p {
    color: #16344e;
    font-size: 16px;
    margin-bottom: 5px;
  }
  .showOnlyMobileCard h1 {
    font-size: 48px;
  }
  .showOnlyMobileCard div {
    padding: 1rem;
  }
  .showOnlyMobileCard h5 {
    color: #16344e;
    font-size: 20px;
    font-weight: 600;
  }
  .exploreAllLink {
    width: 100% !important;
  }
}
